#topbar {
	z-index: 19; }
.topbar {
	padding-top: 2.75rem;
	display: flex;
	justify-content: space-between;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	&__ic {
		cursor: pointer;
		&--close {
			display: none; } }
	&__icons {
		z-index: 2; }
	&__menu {
		position: absolute;
		z-index: 1;
		top: -500%;
		left: 0;
		width: 100%;
		padding: 6rem 0rem 3.87rem 0rem;
		display: flex;
		flex-direction: column;
		align-items: center;
		background: rgba(224, 224, 224, 0.1);
		backdrop-filter: blur(11px);
		transition: top 0.3s ease-out;

		&-item {
			@include font-wide;
			color: $blue;
			text-transform: capitalize;
			font-size: 1.5rem;
			margin-bottom: 2.63rem;
			&:hover {
				text-decoration: none; } } } }
