$sm: 576px;
$md: 768px;
$lg: 992px;
$xl: 1200px;

@import "colors";
@import "fonts";

@import "sections/header";
@import "sections/chat";
@import "sections/portfolio";
@import "sections/mailing";

@import "elements/page";
@import "elements/chat-bar";
@import "elements/circled";
@import "elements/topbar";

@import "elements/page/next-proj";



.border {
	border: 1px solid black;
	&-r {
		border: 1px solid red; } }

body {
	@include regular; }

a.link-w-icon {
	display: flex;
	&::after {
		content: '';
		align-self: center;
		width: 1.625rem;
		height: 1.625rem;
		transform: translate(1rem);
		background: url('../img/link-icon.svg'); } }



h1.title {
	@include font-wide;
	font-style: normal;
	font-weight: 500;
	color: $blue;
	display: flex;
	align-items: center;
	&:after {
		content: ''; } }

.container {
	padding: 0 3rem;
	@media screen and (min-width: $xl) {
		max-width: 1550px; } }
