
.page {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 20;
	display: none;
	overflow: auto;
	&__topbar {
		z-index: 21;
		padding: {
			left: 4.2rem;
			right: 4.2rem; }
		.topbar__ic--close {
			display: block; } }

	&__section {
		padding: {
			left: 4.2rem;
			right: 4.2rem; }
		background: white;

		&-header {
			background: transparent;
			backdrop-filter: blur(50px);
			padding-top: 11.56rem;
			height: 70rem;
			z-index: 1; }

		&-main {
			margin-top: -395px;
			z-index: 20;
			position: relative;
			img {
				width: 100%; } }

		&-next-proj {
			padding: {
				top: 13rem;
				bottom: 13rem; } } }

	&__title {
		font-size: 4.2rem;
		font-weight: 500;
		line-height: 4.2rem;
		color: $blue;
		@include font-wide;
		margin-bottom: 3.3rem; }

	&__descr {
		font-size: 1.35rem;
		line-height: 2.5rem; }

	&__info {
		margin-top: 5.75rem;
		padding: 0; }

	.info {

		&__item-title {
			font-size: 1.5rem;
			@include font-wide;
			color: $blue;
			line-height: 2.5rem;
			margin-bottom: 0.5rem;
			text-transform: uppercase;
			font-weight: 500; }

		&__item-text {
			font-size: 1.35rem;
			color: $gray-2;
			line-height: 2.5rem;
			text-transform: capitalize;
			margin: 0; }
		a.info__item-text {
			text-decoration: underline;
			text-transform: lowercase; }

		&__link {
			display: flex;
			align-items: center;
			justify-content: flex-end;
			a.link-w-icon {
				color: $gray-2;
				font-size: 1.35rem;
				line-height: 2.5rem;
				text-transform: initial;
				&::after {
					background-image: url('../img/link-icon--black.svg');
					margin-left: 0.75rem; }
				&:hover {
					text-decoration: none; } } }
		&__circled {
			width: auto;
			height: auto;
			display: flex;
			align-items: center;
			justify-content: center;
			.circled__circle-area {
				height: 4.19rem;
				width: 4.19rem;
				margin-left: -2rem; } } } }
